.card {
  padding: 30px;
  width: 90%;
  margin: 10px;
  height: 90%;
  position: absolute;
  transform-style: preserve-3d;
  transition: transform 0.5s ease-in-out;
}

.card-container {
  position: relative;
  cursor: pointer;
  width: 18rem;
  height: 18rem;
  padding: 0;
  margin: 50px;
}
.front {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  height: 90%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  margin: auto;
}
.back {
  position: absolute;
  width: 90%;
  height: 90%;
  right: 5%;
  bottom: 5%;
  transform: translate(-5%, -5%);

  -webkit-backface-visibility: hidden;

          backface-visibility: hidden;
  transform: rotateY(180deg);
}
.card:hover {
  transform: rotateY(180deg);
}

